































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component
export default class CartTable extends Vue {
  @Prop({
    default() {
      return [];
    }
  })
  value!: any[];
  checked = false;
  @Watch("value")
  onValueChange(new_val, old_val) {
    this.checked = false;
  }
}
