










































import { Component, Vue, Watch } from "vue-property-decorator";
import DatePicker from "./DatePicker.vue";
import SuggestionInput from "./SuggestionInput.vue";

import {
  getSeckillList,
  buyDirect,
  cartAdd,
  pushServer,
  popServer
} from "../api";
import bus from "../bus";
import { storage } from "../decorators";
import { setTimeout } from "timers";
import { sendMsg } from "../msg";

@Component({
  components: {
    DatePicker,
    SuggestionInput
  }
})
export default class SeckillList extends Vue {
  platform = "taobao";
  list = [];
  url = "";
  from_pc = true;

  pullData() {
    getSeckillList({
      platform: this.platform,
      url: this.url
    }).then(data => {
      // @ts-ignore
      data.forEach(group => {
        group.checked = false;
        group.items.sort((a, b) => b.price - a.price);
        group.items.forEach(item => {
          item.checked = false;
        });
      });
      // @ts-ignore
      this.list = data;
    });
  }
  seckill({ items, qq }, isChecked = false) {
    return Promise.all(
      items.map(item => {
        if (isChecked) {
          if (!item.checked) {
            return;
          }
        }
        return buyDirect(
          {
            url: item.url,
            quantity: 1,
            expectedPrice: +item.seckillPrice,
            forcePrice: true,
            jianlou: 1,
            from_pc: this.from_pc,
            other: {},
            _comment: item.title,
            qq
          },
          item.time,
          this.platform
        );
      })
    );
  }

  selectGroupAll(item, checked) {
    item.items.forEach(item => {
      item.checked = checked;
    });
  }

  addCart(item) {
    cartAdd(
      {
        url: item.url,
        quantity: 1
      },
      this.platform
    );
  }

  mounted() {
    bus.$on("seckill", (data?: { port: number; qq: number }) => {
      if (data) {
        pushServer(data.qq);
      }
      getSeckillList({
        platform: this.platform,
        url: this.url
      })
        // @ts-ignore
        .then(([{ items, time }]) => {
          var t = new Date(time).getTime();
          items = items.sort((a, b) => b.price - a.price);
          this.seckill({
            items: items.slice(0, 3),
            qq: data && data.qq
          }).then(
            () => {
              sendMsg(time + "开始秒杀", data && data.qq);
            },
            e => {
              sendMsg("秒杀出错", data && data.qq);
            }
          );
          if (data) {
            popServer();
          }
        })
        .catch(e => {
          sendMsg(e.message, data && data.qq);
          if (data) {
            popServer();
          }
        });
    });
  }
  beforeDestroy() {
    bus.$off("seckill");
  }
}
